.Contact_label {
  display: inline-block;
  margin-bottom: 16px;
  font-size: 30px;
  color: #171718;
  cursor: pointer;
  margin-right: 10px;
}
.Name {
  margin-right: 10px;
}
.Contact_input {
  /* color: #171718;
  font: inherit;
  font-size: 1.2rem;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #bdbdbd;
  width: 350px;
  outline: 0; */
  width: 200px;
  padding: 10px;
  border: none;
  border-radius: 4px;
  color: #000;
  outline: none;
}
.Contact_input:focus {
  border: 1px solid #2b32b2;
}
.Contact_btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: #3f51b5;
  border: 0;
  outline: 0;
  padding: 8px 24px;
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0px 2px 1px 1px rgba(0, 0, 0, 0.05);
  font: inherit;
  user-select: none;
  transition: background-color 200ms ease-in-out;
}

.Contact_btn:hover {
  box-shadow: 0px 2px 1px 1px rgba(0, 0, 0, 0.15);
  background-color: #303f9f;
}
